.hero {
    .grid-container-hero {
        display: grid;
        grid-template-columns: 1fr 395px;
        grid-template-rows: 79px 109px;
        margin-bottom: 158px;
        column-gap: 30px;
        .title {
            grid-column: 1/1;
            grid-row: 1/-1;
            /* Style for "Find the b" */
            //width: 635px;
            //height: 201px;
            color: #ffffff;

            font-family: "Livvic-Bold";
            font-size: 100px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: normal;
            line-height: 100px;
            text-align: left;
            span {
                color: #f67e7e;
                font-style: normal;
                letter-spacing: normal;
                margin-left: 30px;
                //line-height: normal;
            }
        }
        .desc {
            grid-column: 2/3;
            grid-row: 2/3;
            .desc-txt {
                /* Style for "Finding th" */
                //width: 416px;
                //height: 109px;
                color: #ffffff;
                font-family: "Livvic-SemiBold";
                font-size: 18px;
                font-weight: 600;
                font-style: normal;
                letter-spacing: normal;
                line-height: 28px;
                text-align: left;
            }
            .rectangle {
                width: 50px;
                height: 4px;
                background-color: #79c8c7;
                position: relative;
                top: -73px;
            }
        }
    }
}
